body {
  line-height: 1.5;
  font-family: $roboto;
  font-size: ms(0);
  margin: 0;
  padding: 0;
  background-color: $primary-color;
}


h1 {
  line-height: 1.1;
  font-size: ms(5);

}

h2 {
  line-height: 1.2;
  font-size: ms(4);
}

h3 {
  line-height: 1.3;
  font-size: ms(3);

}

h4 {
  line-height: 1.4;
  font-size: ms(2);
}
.modal-container {
  position: relative;
}
.modal-container .modal, .modal-container .modal-backdrop {
  position: absolute;
}
